import React from "react";
import Login from "./Login";
import appSettings from "../../../appsettings.json";

const encrypt = (value: string): string => {
  // Example encryption function (replace with your actual implementation)
  return btoa(value);
};

const showLoggedInUI = (username: string): void => {
  alert(`Welcome, ${username}!`);
};

// Extract the environment-specific settings
const environment = process.env.NODE_ENV === "production" ? "production" : "development";
const environmentSettings = appSettings[environment];

const App: React.FC = () => {
  return (
    <div>
      <Login appSettings={environmentSettings} encrypt={encrypt} showLoggedInUI={showLoggedInUI} />
    </div>
  );
};

export default App;